body {
  color: #000;
  text-align: center;
  background-color: #fff;
  margin: 0;
  padding-bottom: 15px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

p {
  margin: 0;
  padding: 0;
}

.search-form {
  text-align: center;
  background-color: #989898;
  border: 1px solid rgba(129, 129, 129, .2);
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 12px 16px 12px 42px;
}

.search-form input {
  height: 10px;
  border: 1px solid rgba(255, 255, 255, .2);
  border-radius: 4px;
  margin-right: 10px;
  padding: 12px 16px 12px 42px;
}

.search-form button {
  letter-spacing: .06em;
  color: #989898;
  cursor: pointer;
  min-width: 100px;
  background: #fff;
  border: none;
  border-radius: 4px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.87;
}

.load-more {
  letter-spacing: .06em;
  color: #fff;
  cursor: pointer;
  min-width: 100px;
  background: #989898;
  border: none;
  border-radius: 4px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.87;
}

.gallery {
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.gallery__image:hover {
  transform: scale(1.03);
}

.photo-card {
  margin-bottom: 25px;
}

.photo-card__image {
  height: 100%;
  width: 100%;
  margin-bottom: 10px;
}

.gallery-item {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.info {
  height: max-content;
  display: flex;
}

.info-item {
  margin-right: 10px;
}

.is-hidden {
  display: none;
}

